@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
    'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

* {
  font-family: "Quicksand";
  color: #333;
}

html {
  background: #093028; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #237a57,
    #093028
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.App {
  text-align: center;
}

.convert-container {
  margin: 0 auto;
  background-color: #dcf5ef;
  max-width: 25%;
  height: 500px;
  box-shadow: -3px -2px 45px 17px rgba(0, 0, 0, 0.35);
}

.convert-select {
  padding-top: 6%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.convert-amount {
  margin: 5%;
}

.amount {
  display: block;
  margin-bottom: 2%;
}

.table-container {
  padding-left: 3em;
  padding-right: 3em;
  margin-top: 5em;
  margin-bottom: 2em;
}

.supported-currencies {
  font-size: 28px;
  color: #d9b171;
}

.result-area {
  display: block;
}

.result-area h2 {
  font-size: 30px;
  color: #093028;
}

.amt-total {
  font-size: 36px;
  color: #093028;
  font-weight: bold;
}

table {
  margin: 0 auto;
  padding-top: 1%;
  border-collapse: collapse;
  font-size: 1.1em;
  min-width: 700px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
}

th {
  background-color: rgb(215, 139, 85);
}

td {
  padding: 12px 15px;
}

tr {
  border-bottom: 1px solid #dddddd;
}

tr:nth-of-type(even) {
  background-color: #f3f3f3;
}

tr:nth-of-type(odd) {
  background-color: #dcf5ef;
}

label {
  font-size: 1.2rem;
}

input {
  width: 80%;
  height: 30px;
  font-size: 20px;
}

h1 {
  text-shadow: 1px 2px;
  color: #d9b171;
}

